@font-face {
  font-family: "BalooTammudu2-Regular";
  src: local("BalooTammundu2-Regular"),
    url("./fonts/BalooTammudu2-Regular.ttf") format("truetype");
}

html {
  scroll-behavior: smooth;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

h1 {
  color: rgb(216, 216, 216);
}

h2 {
  font-size: 36px;
}

ul {
  list-style-type: none;
  color: rgb(216, 216, 216);
}

/* underline animation */

a {
  position: relative;
  color: rgb(216, 216, 216);
  text-decoration: none;
}

a:hover {
  color: rgb(216, 216, 216);
}

a::before {
  content: "";
  position: absolute;
  display: block;
  width: 100%;
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: rgb(216, 216, 216);
  transform: scaleX(0);
  transition: transform 0.3s ease;
}

a:hover::before {
  transform: scaleX(1);
}

/* underline animation */

.text {
  font-family: BalooTammudu2-Regular;
  color: rgb(216, 216, 216);
}

p {
  font-size: 26px;
}

.background {
  background-color: rgb(38, 73, 112);
  height: 410vh;
  position: absolute;
  z-index: -2;
  width: 100%;
}

.big-circle {
  height: 1500px;
  width: 1500px;
  border-radius: 50%;
  position: fixed;
  left: -800px;
  top: -300px;
  z-index: -1;
  background-color: rgba(212, 211, 157, 0.025);
}

.little-circle {
  height: 800px;
  width: 800px;
  border-radius: 50%;
  position: fixed;
  right: -150px;
  top: 450px;
  z-index: -1;
  background-color: rgba(212, 211, 157, 0.025);
}

.ham-container {
  display: flex;
  justify-content: flex-end;
  width: 100vw;
  position: fixed;
  z-index: 2;
}

.ham {
  color: rgb(255, 255, 255);
  font-size: 40px;
  width: 30px;
  display: flex;
  flex-direction: column;
  z-index: 2;
  margin-top: 24px;
  margin-right: 24px;
}

.ham-line {
  border: 1.5px solid;
  background-color: rgb(216, 216, 216);
  border-radius: 1px;
  z-index: 2;
  margin-bottom: 6px;
}

.nav {
  position: fixed;
  right: 0;
  height: 100vh;
  width: 100vw;
}

.nav-opened {
  background: transparent;
  position: fixed;
  right: 0;
  transition: opacity 1s ease-in;
  height: 8vh;
  width: 100%;
  display: flex;
  justify-content: right;
  background-color: rgba(31, 31, 31, 0.911);
}

.nav-ul {
  text-align: center;
  height: 100%;
  width: 40%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.nav-li {
  font-size: 24px;
  padding-top: 10px;
}

.landing {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.title-container {
  width: 80%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.landing-title {
  border-bottom: 3px solid rgb(216, 216, 216);
  font-size: 55px;
}

.landing-subtitle {
  font-size: 28px;
  color: rgb(216, 216, 216);
  margin-top: 25px;
}

.about-me {
  height: 100vh;
  padding: 150px;
}

.about-me-title {
  text-decoration: underline;
  font-size: 45px;
  text-align: center;
  height: 150px;
}

.about-me-p-container {
  display: flex;
  justify-content: space-evenly;
}

.about-me-p {
  width: 40%;
}

.stack {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.stacks {
  width: 100%;
  height: 65%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.stack-titles {
  width: 87%;

  display: flex;
  justify-content: space-between;
}

.technologies {
  width: 65%;
  height: 100%;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  padding-top: 60px;
  background-color: rgba(150, 142, 142, 0.356);
  border-radius: 15px;
}

.additions {
  width: 25%;
  height: 100%;
  background-color: rgba(150, 142, 142, 0.356);
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  padding: 45px;
  padding-left: 75px;
}

.tech-list {
  font-size: 25px;
}

.react-logo {
  height: 50px;
}

.javascript-logo {
  height: 60px;
}

.logo-text {
  font-size: 30px;
  text-align: center;
  padding-left: 10px;
}

.logo-line {
  height: 60px;
  display: flex;
  justify-content: center;
  width: 45%;
}

.projects {
  height: 100vh;
  display: flex;
  align-items: center;
}

.big-projects {
  width: 50%;
  height: 100%;
  margin-right: 50px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.project-container {
  height: 40%;
  width: 90%;
  padding: 20px;
  background-color: rgba(150, 142, 142, 0.356);
  border-radius: 15px;
}

.project-description {
  font-size: 18px;
  height: 20%;
}

.project-link {
  width: 100%;
  height: 60%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.project-link:hover {
  cursor: pointer;
}

.big-project-title {
  height: 20%;
}

.website-screen {
  width: 45%;
}

.mini-projects {
  width: 45%;
  height: 75%;
  background-color: rgba(150, 142, 142, 0.356);
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.mini-title {
  font-size: 36px;
  padding-top: 25px;
}

.mini-project-section {
  height: 80%;
  width: 90%;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

.mini-container {
  height: 45%;
  width: 45%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.docs-img {
  width: 45%;
}

.contact {
  height: 10vh;
  background-color: rgba(150, 142, 142, 0.356);
}

.contact-links {
  width: 50%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.each-contact {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.contact-img {
  height: 40px;
  margin-right: 15px;
}

.contact-text {
  padding-top: 5px;
}
